/* waters*/
.leaflet-overlay-pane svg {
    background: rgb(255, 255, 255);
}

/* continents */
.leaflet-interactive {
    filter: drop-shadow(0px 0px 14px rgba(41, 64, 103, 0.87));
    stroke-opacity: 0;
    fill: #F9F9F9;
    fill-opacity: 1;
}

.combined-marker {
    filter: none;
}

.leaflet-control-container {
    display: none;
}
